
.m-nav {
  position: absolute;
  left: 0;
  top: 30%;
  width: 100%;
}
.m-nav p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    text-align: center;
    -webkit-transition: all linear 500ms;
    transition: all linear 500ms;
}
.m-nav p a {
      color: #fff;
      margin: 0 0.78125rem;
      font-size: 0.65625rem;
}
.m-nav .smile {
    font-size: 12px;
}
.m-nav .smile p:nth-child(1) {
      -webkit-transform: rotateZ(25deg);
              transform: rotateZ(25deg);
}
.m-nav .smile p:nth-child(2) {
      -webkit-transform: rotateZ(-25deg);
              transform: rotateZ(-25deg);
}
.m-nav .mouth {
    width: 0.78125rem;
    height: 0.46875rem;
    border-radius: 0.46875rem 0.07813rem 0.46875rem 0.46875rem;
    border: 2px solid #fff;
    margin: 2.96875rem auto 0;
    text-align: center;
    color: #fff;
}
.m-nav .disc {
    margin-top: 1.25rem;
    border-bottom: 2px solid #383933;
}
.m-nav .disc .disc-img {
      width: 1.875rem;
      border: 2px solid #383933;
      border-radius: 1.875rem;
      -webkit-transform-origin: center center;
              transform-origin: center center;
      -webkit-animation: discAniRotate 5000ms linear infinite;
              animation: discAniRotate 5000ms linear infinite;
}
@-webkit-keyframes discAniRotate {
0% {
    -webkit-transform: translateX(-100%) rotate(0deg);
            transform: translateX(-100%) rotate(0deg);
}
100% {
    -webkit-transform: translateX(10rem) rotate(700deg);
            transform: translateX(10rem) rotate(700deg);
}
}
@keyframes discAniRotate {
0% {
    -webkit-transform: translateX(-100%) rotate(0deg);
            transform: translateX(-100%) rotate(0deg);
}
100% {
    -webkit-transform: translateX(10rem) rotate(700deg);
            transform: translateX(10rem) rotate(700deg);
}
}



