
.tooltip-box{
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background-color: rgba(0,0,0,.5);
}
.tooltip-box div{
	position: absolute;
	top: 43%;
	width: 100%;
	color: #fff;
	text-align: center;
	font-size: .55rem;
	padding: 0 12px;
}



