
.detail-box {
  overflow: hidden;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.detail-box background-attachment:fixed
section {
    margin-bottom: 0.53125rem;
}
.hot-m-list {
  color: #fff;
}
.hot-m-intro {
  color: #233023;
}
.m-cover-text {
  overflow: hidden;
}
.m-cover-text .cover-img-box {
    float: left;
    width: 2.34375rem;
    height: 3.125rem;
    overflow: hidden;
    margin: 0 0.21875rem 0.07813rem 0;
}
.m-cover-text .cover-text p {
    word-break: break-all;
    word-wrap: break-word;
    margin-bottom: 0.07813rem;
}
.m-cover-text .cover-text p.text-info {
      text-indent: 2em;
      line-height: 0.5rem;
}
.white-bg-box {
  padding: 0.1875rem;
  border-radius: 8px;
  background-color: #fff;
  width: 94%;
  margin: 0 auto 0.375rem;
}
.white-bg-box h4 {
    margin: 0 0 0.07813rem;
    font-size: 0.34375rem;
}



