
.m-slide-list ul {
  overflow-x: auto;
  overflow-y: hidden;
  height: 4.21875rem;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
  font-size: 0;
}
.m-slide-list li {
  display: inline-block;
  width: 2.34375rem;
  margin: 0 0.09375rem;
  padding: 3px;
}
.m-slide-list li.nodata {
    text-align: center;
    font-size: 0.34375rem;
    padding: 0.3125rem 0;
}
.m-slide-list li.router-link-exact-active {
    padding: 0;
    border: 3px solid #fff;
}
.m-slide-list li .img-box {
    height: 3.125rem;
    overflow: hidden;
}
.m-slide-list li p {
    font-size: 0.34375rem;
    text-align: center;
    margin: 0.15625rem 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.m-slide-list .no-data {
  text-align: center;
  padding: 0.625rem 0;
}



