
.actor-box {
  color: #fff;
}
.actor-box section {
    padding: 0 0.1875rem;
    margin-bottom: 0.78125rem;
    overflow: hidden;
}
.actor-box .cover-box {
    float: left;
    width: 2.34375rem;
    height: 3.125rem;
    overflow: hidden;
    margin: 0 0.21875rem 0.07813rem 0;
}
.actor-box .text-info {
    line-height: 0.625rem;
}
.actor-box .text-info li {
      word-break: break-all;
      word-wrap: break-word;
}



