
.loading-box {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 999;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  font-size: .88rem;
}
.loading-box span {
    display: inline-block;
    margin: 0 2px;
}
.loading-box span:nth-child(1) {
      -webkit-animation: bounce 1500ms linear infinite;
              animation: bounce 1500ms linear infinite;
}
.loading-box span:nth-child(2) {
      -webkit-animation: bounce 1500ms linear 120ms infinite;
              animation: bounce 1500ms linear 120ms infinite;
}
.loading-box span:nth-child(3) {
      -webkit-animation: bounce 1500ms linear 240ms infinite;
              animation: bounce 1500ms linear 240ms infinite;
}
@-webkit-keyframes bounce {
0, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
20% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
    opacity: .2;
}
50% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
}
@keyframes bounce {
0, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
20% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
    opacity: .2;
}
50% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
}



