
.main-bg{
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background-repeat: repeat;
	background-color: #000;
	background-position: center center;
}
.main-bg div{
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.6);
}



